import React, { useState } from "react";
import { View, Text, StyleSheet, TouchableOpacity, Modal } from "react-native";
import AppHeader from "../Custom/AppHeaders";
import I18n from "../../i18n";
import { Calendar } from "react-native-calendars";

function BusRequest(props) {
  const [startVisiable, setStartVisiable] = useState(false);
  const [startDate, setStartDate] = useState();
  const getDATE = new Date();
  const NOW =
    getDATE.getMonth() < 10 || getDATE.getDate() < 10
      ? getDATE.getFullYear() +
        "-0" +
        (getDATE.getMonth() + 1) +
        "-0" +
        getDATE.getDate()
      : getDATE.getFullYear() +
        "-" +
        (getDATE.getMonth() + 1) +
        "-" +
        getDATE.getDate();
  return (
    <>
      <AppHeader
        navigation={props.navigation}
        title={I18n.t("BusRequest")}
        isLeft={true}
      ></AppHeader>
      <View
        style={{
          flex: 1,
          backgroundColor: "white",
        }}
      >
        <View style={{ flex: 1 }}>
          <View style={{ flex: 1, borderWidth: 1 }}>
            <View
              style={{
                flex: 2,
                borderWidth: 1,
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <Text style={{ fontSize: "100%", fontWeight: "bold" }}>날짜</Text>
            </View>
            <TouchableOpacity
              onPress={() => setStartVisiable(true)}
              style={{
                flex: 4,
                borderWidth: 1,
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <Text style={{ fontSize: 30, fontWeight: "bold" }}>
                {startDate}
              </Text>
            </TouchableOpacity>
            <Modal visible={startVisiable}>
              <View
                style={{
                  flex: 1,
                  backgroundColor: "rgba(0,0,0,0.5)",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <View
                  style={{
                    height: "50%",
                    width: "50%",
                    backgroundColor: "white",
                  }}
                >
                  <Calendar
                    current={NOW}
                    minDate={NOW}
                    onDayPress={(day) => {
                      setStartDate(day.dateString);
                      setStartVisiable(false);
                    }}
                    monthFormat={"yyyy MM"}
                  />
                </View>
              </View>
            </Modal>
          </View>
          <View style={{ flex: 1, borderWidth: 1 }}>
          <View
              style={{
                flex: 2,
                borderWidth: 1,
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <Text style={{ fontSize: "100%", fontWeight: "bold" }}>날짜</Text>
            </View>
            <TouchableOpacity
              onPress={() => setStartVisiable(true)}
              style={{
                flex: 4,
                borderWidth: 1,
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <Text style={{ fontSize: 30, fontWeight: "bold" }}>
                {startDate}
              </Text>
            </TouchableOpacity>
          </View>
          <View style={{ flex: 1, borderWidth: 1 }}></View>
          <View style={{ flex: 1, borderWidth: 1 }}></View>
          <View style={{ flex: 1, borderWidth: 1 }}></View>
        </View>
      </View>
    </>
  );
}

export default BusRequest;
